








































































































import axios from '@/api/axios';
import { MaterialGoods } from '@/models/MaterialGood';
import Paging from '@/models/Paging';
import {
  defineComponent, ref, watch, computed,
} from '@vue/composition-api';
// @ts-ignore
import Intersect from 'vue-intersect';
import { O } from 'ts-toolbelt';
import objectToFormData from '@4dst-saas/public-utils/dist/object-to-form-data';
import errorHandler from '@/utils/error-handler';
import AppError from '@/utils/error';
import i18next from 'i18next';
import urlToBlob from '@/utils/url-to-blob';
import DisplayModeSwitcher from '../components/DisplayModeSwitcher.vue';
import Navigator from '../components/Navigator.vue';
import MaterialItem from '../components/CommodityItem.vue';
import CompanyList from '../Home/components/CompanyList.vue';
import SearchParams, { SearchParamsValue } from './SearchParams.vue';

interface Company {
  id: String,
  logo: String,
  mail: String,
  contact: String,
  address: String,
  materialList: MaterialGoods[],
  [propName: string]: any
}

export default defineComponent({
  components: {
    CompanyList,
    SearchParams,
    Navigator,
    MaterialItem,
    Intersect,
    DisplayModeSwitcher,
  },
  props: {
    searchParamsStr: {
      type: String,
      required: true,
    },
    file: {
      type: [Object as () => File, String],
    },
  },
  inject: ['isQt'],
  setup(props, { root }) {
    const pageSize = 30;
    const displayMode = ref('drape');
    const emptyPage = {
      size: pageSize,
      current: 0,
      records: undefined,
      total: undefined,
      pages: undefined,
      isLoading: false,
    };


    const paging = ref<Paging<MaterialGoods>>({
      ...emptyPage,
    });
    const error = ref<Error>();
    const canLoadMore = computed(() => {
      const vPaging = paging.value;
      return !error && !vPaging.isLoading && (vPaging.pages === undefined || vPaging.current < vPaging.pages);
    });
    const load = async (
      searchParams: SearchParamsValue,
      current = paging.value.current + 1,
      handleRecords = (oldRecords: MaterialGoods[] | undefined, newRecords: MaterialGoods[]) => {
        if (!oldRecords) {
          return newRecords;
        }
        return oldRecords.concat(newRecords);
      },
    ) => {
      // v-if="paging.records && !paging.records.length"
      try {
        const vPaging = paging.value;
        paging.value = {
          ...vPaging,
          size: pageSize,
          current,
          isLoading: true,
        };

        const { type, ...__searchParams } = searchParams;

        if (type === 'STORE') {
          const p = (await axios.get<Paging<Company>>('/authcenter/company/search', {
            params: {
              keyword: searchParams.condition,
              pageNumber: paging.value.current,
              pageSize: paging.value.size,
            },
          })).data;
          await Promise.all(p.records.map(async company => {
            company.materialList = (await axios.get<MaterialGoods[]>('/mall/mall/company/list', {
              params: {
                companyId: company.id,
              },
            })).data;
          }));
          Object.assign(paging.value, {
            ...p,
            isLoading: false,
          });
        } else {
          const {
            currentCategoryPaths, file, ..._searchParams
          } = __searchParams;
          type T = O.Required<Paging<MaterialGoods>, 'records'>;
          if (!file) {
            const id = currentCategoryPaths[currentCategoryPaths.length - 1]?.id ?? currentCategoryPaths[currentCategoryPaths.length - 2]?.id;
            const category = id ? (await axios.post<string[]>('show/category/allChildWithSelf', [id])).data : [];
            const p = (await axios.post<T>('mall/mall/search', {
              ..._searchParams,
              category,
              page: paging.value.current,
              size: paging.value.size,
            })).data;
            Object.assign(paging.value, {
              ...p,
              records: handleRecords(vPaging.records, p.records),
              isLoading: false,
            });
          } else {
            Object.assign(paging.value, {
              records: (await axios({
                url: '/bff-center/s/mall/pic',
                method: 'POST',
                data: objectToFormData({
                  file,
                }),
              })).data as MaterialGoods[],
            });
          }
        }


        if (!paging.value?.records?.length) {
          throw new AppError({ code: 404, message: i18next.t('public.no_result') });
        }
      } finally {
        paging.value.isLoading = false;
      }
    };
    const search = async (searchParams: SearchParamsValue) => {
      error.value = undefined;
      paging.value = { ...emptyPage };
      try {
        await load(searchParams, 1);
      } catch (e) {
        error.value = e;
      }
    };

    const searchParams = ref<SearchParamsValue>();
    watch(() => {
      const result: SearchParamsValue = JSON.parse(props.searchParamsStr);
      result.applicableProductLine ??= [];
      result.designContent ??= [];
      result.surfaceTechnics ??= [];
      result.materialsTechnology ??= [];
      result.currentCategoryPaths ??= [];
      result.currentCategoryPaths[0] ??= { name: '全部', id: null };
      result.type ??= 'MATERIAL';
      result.condition ??= '';
      result.file = props.file ?? undefined;
      return result;
    }, async (result) => {
      error.value = undefined;
      paging.value = {
        size: pageSize,
        current: 0,
        records: undefined,
        total: undefined,
        pages: undefined,
        isLoading: false,
      };
      try {
        if (typeof result.file === 'string') {
          try {
            const file = await urlToBlob(result.file);
            searchParams.value = { ...result, file };
          } catch (e) {
            searchParams.value = { ...result, file: undefined };
            throw new AppError('当前搜索图片已失效，请重新搜索');
          }
        } else {
          console.log(result);
          searchParams.value = result;
        }
        paging.value = { ...emptyPage };
        await load(searchParams.value, 1);
      } catch (e) {
        error.value = e;
      }
    }, {
      immediate: true,
    });

    const loadMore = async () => {
      try {
        await load(searchParams.value);
      } catch (e) {
        errorHandler(e);
      }
    };
    const searchParamsChange = async (_searchParams: SearchParamsValue) => {
      if (_searchParams.file) {
        Object.assign(searchParams.value, _searchParams);
        await search(_searchParams);
      } else {
        console.log(_searchParams);
        try {
          await root.$router.replace({
            query: {
              searchParamsStr: JSON.stringify({ ..._searchParams, file: undefined }),
              file: null,
            },
          });
        } catch (e) {
          // 路由相同时，刷新数据，用于以图搜图取消图片或者使用其他搜索的情况
          if (e.name === 'NavigationDuplicated') {
            searchParams.value.file = undefined;
            await search(_searchParams);
          }
        }
      }
    };

    async function preview(index: number) {
      const srcList = paging.value.records!.map((material: any) => material.imgJson.thumbnail);
      // @ts-ignore
      await root.$ImageViewer.show(srcList, {
        index,
        targetElems: '[data-img-type=\'material-item\']',
      });
    }


    return {
      displayMode,
      error,
      loadMore,
      load,
      canLoadMore,
      search,
      searchParamsChange,
      paging,
      searchParams,
      preview,
    };
  },
});
