<template>
  <div class="navigator"
    :class="{'qt-page': $route.path.startsWith('/qt/'), 'hide-background': hideBackground}">
    <span class="back" @click="back">
      <i class="iconfont icon-angle-left"></i>{{ $t('material-mall.previous') }}
    </span>
    <span class="page-name">
      <slot></slot>
    </span>
  </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api';
import { isQt } from '@/utils/is-qt';

export default defineComponent({
  props: {
    hideBackground: {
      type: Boolean,
      default: false,
    },
  },
  setup(_, { root }) {
    // p.s. 打开新窗口步骤是先打开空页面在跳路由
    if (window.history.length <= 2 && !window._g_navigator_history_key) {
      window._g_navigator_history_key = window.history.state.key;
    }
    function back() {
      if (window.history.state.key !== window._g_navigator_history_key) {
        root.$router.back();
      } else {
        root.$router.push(`${isQt() ? '/qt' : ''}/material-mall/home`);
      }
    }
    return {
      back,
    };
  },
});
</script>

