.navigator{
  position: sticky;
  z-index: 3;
  top: 60px;
  user-select: none;
  width: 100%;
  height: 44px;
  background:#f9f9f9;
  line-height: 44px;
  box-shadow:0px 2px 4px 0px rgba(0,0,0,0.1);
  // opacity:0.75;
  &.hide-background{
    background: transparent;
    border-bottom: 1px solid transparent;
    box-shadow: none;
  }
}

.qt-page{
  top: 0
}

.back{
  margin-right: 10px;
  cursor: pointer;
  &:hover{
    color: $color-primary;
  }
}

.icon-angle-left{
  padding: 0 10px;
}
.page-name{
  padding-left: 10px;
  border-left: 1px solid #666;
}

</style>
